<template>
<img :src="imgData" class="svg-icon" @click="handleClick"/>
</template>
<script>
export default {
  name: 'iconu',
  props: {
    iconName: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      imgData: undefined
    }
  },
  watch: {
    iconName: {
      handler (value) {
        this.imgData = this.$store.getters.getIconByName(value)
      }
    }
  },
  created () {
    this.imgData = this.$store.getters.getIconByName(this.iconName)
  },
  methods: {
    handleClick () {
      this.$emit('click')
    }
  }
}
</script>
<style scoped>
.svg-icon {
  width: 1.5em;
  height: 1.5em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
.svg-external-icon {
  background-color: currentColor;
  mask-size: cover!important;
  display: inline-block;
}
</style>



