const icon = {
  namespace: true,
  state () {
    return {
      icons: {}
    }
  },
  getters: {
    getIconByName: (state) => (payload) => {
      if (typeof payload === 'string') {
        return state.icons[payload]
      }
      return undefined
    }
  },
  actions: {
    initIcon ({ state, commit }) {
      if (Object.keys(state.icons).length) {
        return
      }
      const req = require.context('@/assets/icon', false, /\.svg$/)
      const requireAll = requireContext => requireContext.keys().map(requireContext)
      const arr = requireAll(req)
      req.keys().forEach((k, idx) => {
        state.icons[k.substring(k.lastIndexOf('/') + 1, k.lastIndexOf('.svg'))] = arr[idx]
      })
    }
  }
}

export default icon



